import './App.css'
import ExpenseItem from './Components/ExpenseItem';
function App(){

    // let expenseDate = new Date(2021,3,28);
    // let expenseTitle = "School Fee";
    // let expenseAmount = 300;  

    let expense = [
        {
            id:'e1',
            title:'School Fee',
            amount:250,
            date:new Date(2021,5,11)
        },
        {
            id:'e2',
            title:'Room Fee',
            amount:300,
            date:new Date(2021,5,12)
        },
        {
            id:'e3',
            title:'Tution Fee',
            amount:350,
            date:new Date(2021,5,13)
        },
        {
            id:'e4',
            title:'Extra Fee',
            amount:400,
            date:new Date(2021,5,14)
        }
    ];


    return (
        <div>
            <h2>Get Started</h2>
            <ExpenseItem 
            date={expense[0].date} 
            title={expense[0].title} 
            amount={expense[0].amount}>
            </ExpenseItem>

            <ExpenseItem 
            date={expense[1].date} 
            title={expense[1].title} 
            amount={expense[1].amount}>
            </ExpenseItem>

            <ExpenseItem 
            date={expense[2].date} 
            title={expense[2].title} 
            amount={expense[2].amount}>
            </ExpenseItem>

            <ExpenseItem 
            date={expense[3].date} 
            title={expense[3].title} 
            amount={expense[3].amount}>
            </ExpenseItem>
        </div>
    );
}

export default App;